*,
*:before,
*:after {
  box-sizing: border-box;
  outline: unset !important;
}

*,
*:focus,
*:hover,
*:active,
*::before,
*::after,
*::focus-visible {
  outline: unset !important;
}

a,
a:hover,
a:active,
a:focus,
a:focus-visible,
a:visited {
  text-decoration: none;
}
button,
form,
input,
select,
table,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  font-size: 16px;
  outline: unset;
  resize: none;
}

.header-space {
  margin-top: 160px;
}
.dynamic-sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 45px;
  transition: all 0.45s ease !important;
}

.swiper-pagination-bullet .path {
  stroke-dashoffset: 120;
  stroke-dasharray: 120;
}
.swiper-pagination-bullet-active .path {
  stroke-dasharray: 1000;
  stroke-dashoffset: 0;
  animation: dash linear 70s;
  animation-iteration-count: unset;
  stroke: white;
}

@keyframes dash {
  from {
    stroke-dashoffset: 1000;
  }

  to {
    stroke-dashoffset: 0;
  }
}

.image-popup,
.video-popup {
  cursor: pointer;
  display: flex;
  height: 100%;
  position: relative;
  width: 100%;
}

.image-popup:after,
.video-popup:after {
  background-color: rgba(0, 0, 0, 0.3);
  bottom: unset;
  content: "";
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  right: unset;
  top: 0;
  transition: all 0.45s ease;
  width: 100%;
  z-index: 1;
}


.image-popup img,
.video-popup img {
  transition: all 0.45s ease;
}

.image-popup:hover img,
.video-popup:hover img {
  transform: scale(1.1);
}

.image-popup:hover:after,
.video-popup:hover:after {
  opacity: 1;
}

.image-popup:hover:before,
.video-popup:hover:before {
  opacity: 1;
  transform: translateX(-50%) translateY(-50%);
}

.image-popup.no-scale img,
.video-popup.no-scale img {
  transform: scale(1);
}

.image-popup.no-shadow:after,
.video-popup.no-shadow:after {
  opacity: 0;
}

.video-popup:before {
  background-image: url("data:image/svg+xml;base64,");
  opacity: 1;
  transform: translateX(-50%) translateY(-50%);
}
.image-zoom img {
  transition: all 0.45s ease;
}

.image-zoom:hover img {
  transform: scale(1.1);
}

.video-content 
{
    position: relative;
    overflow: hidden;
}


.video-content iframe 
{
    /* via: https://stackoverflow.com/questions/21314206/full-width-vimeo-wrapper-background */
    width: 100vw;
    height: 56.25vw;
    min-height: 100vh;
    min-width: 177.77vh;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
}

.fancybox__backdrop {
  background-color: rgba(255,255,255);
}
.fancybox__infobar {
  color: rgba(33,33,33,.98);
}


.stars {
  display: flex;
  gap: 10px;
}

.stars .rating-star {
  cursor: pointer;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.stars .rating-star svg {
  width: 20px;
  height: 20px;
  fill: #0055A320;
  transition: 0.45s;
}

.stars .rating-star.to-rate svg {
  fill: #0055A3;
}

.stars .rating-star.rated svg {
  fill: #0055A3;
}

.stars .rating-star.no-to-rated svg {
  fill: #d0e8f0;
}

.stars .rating-star.to-hover svg {
  fill: #0055A3;
}

