

@font-face {
  font-family: "Montserrat";
  src: url("../font/montserrat/Montserrat-VariableFont_wght.ttf")
      format("truetype-variations");
  font-weight: 1 999;
  font-style: normal;
  font-display: swap;
  ascent-override: 100%;
}

@font-face {
  font-family: "Montserrat";
  src: url("../font/montserrat/Montserrat-Italic-VariableFont_wght.ttf")
      format("truetype-variations");
  font-weight: 1 999;
  font-style: italic;
  font-display: swap;
  ascent-override: 100%;
}


@font-face {
  font-family: 'icon font';
  src: url('../font/iconfont/icon-font.eot');
  src: url('../font/iconfont/icon-font.eot?#iefix') format('embedded-opentype'),
       url('../font/iconfont/icon-font.woff') format('woff'),
       url('../font/iconfont/icon-font.ttf') format('truetype'),
       url('../font/iconfont/icon-font.svg#icon-font') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class*='icon-']:before{
display: inline-block;
 font-family: 'icon font';
 font-style: normal;
 font-weight: normal;
 line-height: 1;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale
}
